@use "../../variables/colors" as *;
@use "../../../colors" as *;
@use "../../variables/sizes" as *;
@use "../../../sizes" as *;
@use "../../../tabs/variables/colors" as *;
@use "../../../tabs/variables/borders" as *;
@use "../../../tabs/variables/sizes" as *;
@use "../../../tabs/mixins" as *;
@use "../../../tabs" as *;
@use "../../../../base/tabPanel";
@use "../../variables/borders" as *;

// adduse

.dx-tabpanel-tabs-position-right {
  > .dx-tabpanel-tabs {
    .dx-tabs-wrapper {
      border-inline-start: $generic-tabs-border;
      border-inline-end: $generic-tabs-border;
    }

    .dx-tabs-scrollable {
      @include dx-tabs-border-mixin(
        $generic-tabs-border-width solid,
        none,
        $generic-tabs-border-width solid,
        none,
      );

      &::after {
        right: 0;
        left: $generic-tabs-border-width;
      }
    }
  }

  &:not(.dx-empty-collection) {
    > .dx-tabpanel-container {
      > .dx-multiview-wrapper {
        border-inline-end: none;
      }
    }
  }
}
